<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

      <van-row class="title text_center">
        <van-col span="7">Tenor</van-col>
        <van-col span="8">Bid/Vol.Bid</van-col>
        <van-col span="8">Ofr/Vol.Ofr</van-col>
      </van-row>

      <van-collapse class="list" v-model="activeNames">
        <van-collapse-item :title="listTitle[index]" :name="index" v-for="(item,key,index) in list">
          <van-row class="text_center" v-for="item2 in item">
            <van-col span="6">{{item2.tenor}}</van-col>
            <van-col span="5">{{item2.bid}}</van-col>
            <van-col span="4">{{item2.bidVol}}</van-col>
            <van-col span="5">{{item2.offer}}</van-col>
            <van-col span="4">{{item2.ofrVol}}</van-col>
          </van-row>
        </van-collapse-item>
      </van-collapse>

    </van-pull-refresh>

  </div>
</template>

<script>
  export default {
    name: "interestRate",
    data() {
      return {
        title: '利率互换',
        isLoading: false,
        activeNames: [0, 1, 2, 3, 4],
        listTitle: ['IRS-SHIBOR 3M', 'IRS-FR007', 'IRS-Spread SHIBOR 3M', 'IRS-Spread FR007', 'IRS-Basis SHIBOR 3M*FR007'],
        list: []
      };
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      onRefresh() {
        this.getData(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
        });
      },
      getData(fn) {
        this.$http.post(backstageIp + userUrl + '/getFlagshipData',
          {
            url: cfaeIp + '/mall/rate/getRateList.json?authorization=',
            type:'get'
          }
        ).then(res => {
          if(res.data.success){
            this.list = JSON.parse(res.data.data).data;
            fn && fn();
          }else{
            this.$toast('当前无网络连接或网络不稳定，请稍后重试');
          }
        }).catch(err => {
          this.$toast('当前无网络连接或网络不稳定，请稍后重试');
        });
      }
    },
    created() {
      this.getData();
    }
  }
</script>

<style scoped>

  .van-pull-refresh {
    margin-top: 46px;
    background: rgba(247, 248, 250, 1);
  }
  >>>.van-pull-refresh__track {
    min-height: calc(100vh - 46px);
  }

  .title {
    background: rgba(255, 255, 255, 1);
  }
  .title .van-col {
    line-height: 40px;
    font-size: 15.5px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(117, 117, 117, 1);
    /*text-align: center;*/
  }

  .list .van-collapse-item {
    padding-top: 10px;
  }
  >>>.list .van-cell {
    font-size: 15px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(32, 32, 32, 1);
    line-height: 21px;
  }
  >>>.list .van-cell__left-icon,
  >>>.list .van-cell__right-icon {
    height: 21px;
    line-height: 21px;
  }
  >>>.list .van-cell:not(:last-child)::after {
    display: none;
  }
  >>>.list .van-collapse-item__content {
    padding: 0 15px 10px;
  }
  .list [class*=van-hairline]::after {
    display: none;
  }
  .list .van-col {
    font-size: 14px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(221, 169, 68, 1);
    line-height: 20px;
    /*text-align: center;*/
  }
  .list .van-col:first-child {
    font-size: 14px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(124, 124, 125, 1);
    line-height: 20px;
  }

</style>



